import emptyView from '../../components/emptyview.vue';
import PageButton from '../../components/button.vue';

export default {
	data() {
		return {
			list: [],
			pageNum: 1,
			pageSize: 10,
			total: 1,
			payStates:['支付中','支付成功','支付失败','退款成功']
		}
	},
	components: {
		emptyView,
		PageButton
	},
	onLoad(options) {


	},
	onShow() {
		this.pageNum = 1;
		this.total = 1;
		this.list = [];
		this.getUserGene();
	},
	onReady() {

	},
	onReachBottom() {
		this.getUserGene();
	},

	methods: {
		getUserGene:function() {
			let that = this;
			if (that.total <= (that.pageNum - 1) * that.pageSize) {
				return;
			}
			const params = {};
			params.pageNum = that.pageNum;
			params.pageSize = that.pageSize;
			that.$util.request({
				url: "/didi-patient/inquirygeneinfo/queryPage",
				param: params,
				success: function(res) {
					that.list = that.list.concat(res.data.list);
					that.pageNum += 1;
					that.total = res.data.total;
				},
				error: function() {}
			})
		},
		payInquiry:function(inquiryGeneId){
			let that = this;
			var temp = {};
			temp.inquiryGeneId = inquiryGeneId;
			that.$util.request({
				url: "/didi-patient/patientpayinfo/unifiedOrder",
				param: temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					var data = res.data;
					 //调用微信支付
					uni.requestPayment({
					  'appId': data.appId,
					  'timeStamp': data.timeStamp,
					  'nonceStr': data.nonceStr,
					  'package': data.package,
					  'signType': data.signType,
					  'paySign': data.paySign,
					  'success': function (res) {
					    util.showSuccessToast("支付成功");
					    setTimeout(function () {
							uni.navigateBack({
								delta:1
							})
					    }, 1000);
					  },
					  'fail': function (res) {
					    uni.showToast({
					      title: res.message,
					      icon: 'none'
					    })
					  }
					})
				},
				error: function() {}
			})
		}
		
	}
}
